@charset "utf-8";

/*顶部 登录注册内容*/

#loginbox {
    width: 100%;
    background: #f6f6f6;
    .login {
        @include gongy(30px);
        ul {
            width: 312px;
            height: 30px;
            padding: 0;
            float: left;
            li {
                @include fontS(30px, 10px, #474747);
                float: left;
                list-style: none;
                a {
                    color: #474747;
                }
                a:hover {
                    color: red;
                }
            }
        }
        span {
            margin: 0 10px;
            color: #d3ccd3;
            height: 10px;
            width: 1px;
            @include fontS(30px, 12px, #d3ccd3);
            float: left;
        }
        #top-right {
            float: right;
            a {
                @include fontS(30px, 12px, #474747);
                float: left;
            }
            a:hover {
                color: red;
            }
            div {
                float: left;
            }
            span {
                margin: 0 10px;
                color: #d3ccd3;
                @include fontS(30px, 12px);
            }
        }
    }
}


/* header logo 和搜索框*/

#logo {
    background-size: 100% 100%;
    background: url('https://icon.zol-img.com.cn/ucdn/b-zol-com/1.0.2/img/logo.34b1b031.png') no-repeat;
    float: left;
    width: 329px;
    height: 58px;
    text-indent: -9999px;
    margin: 32px 15px 0 0;
}

#topbox {
    width: 100%;
    background: #fff;
}

.top {
    @include gongy(112px);
    background: #fff;
    position: relative;
    .search {
        width: 600px;
        height: 58px;
        float: left;
        margin: 30px 0 0 80px;
        .search-top {
            width: 596px;
            height: 34px;
            border: 2px solid #fd4165;
            border-radius: 23px;
            overflow: hidden;
            span {
                display: block;
                width: 46px;
                height: 38px;
                float: left;
                text-align: center;
            }
            .icon-49 {
                line-height: 38px;
                font-size: 20px;
                color: #d3ccd3;
            }
            input {
                margin-top: 1px;
                width: 480px;
                height: 30px;
                float: left;
                border: none;
                color: red;
            }
            .bn {
                float: right;
                display: block;
                width: 65px;
                height: 38px;
                background: linear-gradient(90deg, #fd4165, #fc002d);
                text-align: center;
                line-height: 38px;
                font-size: 16px;
                color: #fff;
            }
        }
        .search-bottom {
            margin-left: 10px;
            margin-top: 2px;
            width: 600px;
            height: 16px;
            .item {
                display: inline-block;
                @include fontS(16px,
                12px,
                #666);
            }
            span {
                display: inline-block;
                margin: 0 10px;
                font-size: 12px;
                text-align: center;
            }
        }
    }
    #imgbox {
        float: right;
        margin-top: 17px;
    }
}


/* 导航条及二级三级导航*/

#navbox {
    width: 100%;
    background: red;
    .nav {
        position: relative;
        @include gongy(35px);
        background: #fc002d;
        #nav-one {
            width: 230px;
            height: 35px;
            float: left;
            background: #df022d;
            margin-right: 10px;
            .hea {
                width: 230px;
                height: 35px;
                display: inline-block;
                @include fontS(35px,
                16px,
                #fff);
            }
        }
        .nav-item {
            display: inline-block;
            width: 115px;
            height: 35px;
            @include fontS(35px,
            16px,
            #fff)
        }
        .nav-item:hover {
            background: #df022d;
        }
    }
}

#nav-one .nav-one-list {
    z-index: 100;
    width: 230px;
    height: 400px;
    background: #fff;
    @include fontS(35px,
    12px,
    block);
    box-shadow: 0 0 8px 1px rgba(0, 0, 0, .08);
    li {
        z-index: 100;
        width: 230px;
        height: 36px;
        span {
            display: inline-block;
            color: block;
            float: left;
        }
        .iconfont {
            display: inline-block;
            float: left;
            margin-left: 12px;
            margin-right: 10px;
            font-size: 18px;
            color: #aaa;
            width: 17px;
            height: 18px;
        }
    }
    li:hover {
        background: red;
        color: #fff;
        .category-box {
            display: block;
        }
    }
}

.category-box {
    overflow: hidden;
    z-index: 1000;
    display: none;
    width: 740px;
    height: 386px;
    border: 2px solid red;
    background: #fff;
    position: absolute;
    left: 230px;
    top: 35px;
    padding-top: 10px;
    dl {
        width: 600px;
        padding-left: 30px;
        dt {
            margin-top: 10px;
            display: block;
            height: 26px;
            @include fontS(26px,
            14px,
            red);
            text-align: left;
            a {
                color: red;
            }
        }
        dd {
            overflow: hidden;
            text-align: left;
            a {
                float: left;
                margin-right: 15px;
                color: #333;
                font-size: 12px;
                line-height: 24px;
            }
            a:hover {
                color: red;
            }
        }
    }
}