@charset "utf-8";
@mixin gongy( $height:auto, $width:1200px) {
    margin: 0 auto;
    width: $width;
    height: $height;
}

@mixin fontS($lineheight, $fontS:16px, $colors:black) {
    font-size: $fontS;
    line-height: $lineheight;
    color: $colors;
    text-align: center;
}

@import "public";
@import "header";
@import "footer";
.nav-one-list {
    display: none;
}

#nav-one:hover .nav-one-list {
    display: block;
}

.homelist_title {
    width: 100%;
    background: #f8f8f8;
    .homelist_title_warp {
        @include gongy(30px);
        padding-top: 7px;
        a {
            @include fontS(16px, 12px, #666)
        }
        span {
            display: inline-block;
            height: 16px;
            a {
                color: #999;
            }
            font-size: 10px;
            em {
                margin: 0 7px;
            }
        }
        span:nth-of-type(1) {
            width: 61px;
            a {
                color: #666;
            }
        }
        span:nth-of-type(2) {
            width: 85px;
        }
        .pp:hover {
            color: red;
        }
    }
}

.imgc {
    float: left;
}

.goos_xiangq {
    @include gongy(580px);
    position: relative;
    .imgc {
        width: 454px;
        height: 536px;
        position: relative;
        overflow: hidden;
    }
    .big_img {
        width: 2400px;
        height: 448px;
        position: absolute;
        background: yellow;
        border: 1px solid #f0f0f0;
        p {
            width: 450px;
            height: 450px;
            display: inline-block;
            text-align: center;
            line-height: 450px;
            border: 1px solid #f0f0f0;
            width: 450px;
            height: 450px;
            background: #fff;
            float: left;
            margin: 0;
            padding: 0;
            img {
                vertical-align: middle;
                width: 380px;
                height: 380px;
            }
        }
    }
    .samil_img {
        width: 440px;
        height: 73px;
        .img_con {
            width: 376px;
            margin-top: 460px;
            height: 73px;
            position: absolute;
            left: 0;
            top: 0;
            margin-left: 32px;
            p {
                float: left;
                width: 60px;
                height: 60px;
                margin-right: 14px;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .a_red {
                border: 2px solid red;
            }
        }
        .img_bn {
            position: relative;
            margin-top: 460px;
            width: 450px;
            span {
                // margin-top: -453px;
                color: #fff;
                display: block;
                position: absolute;
                font-size: 60px;
                height: 65px;
            }
            .left {
                top: 5px;
                left: 5px;
            }
            .right {
                right: 5px;
                top: 5px;
            }
            .left::after {
                width: 20px;
                height: 20px;
                border: 2px solid #999;
                border-right: none;
                border-top: none;
                content: '';
                display: block;
                transform: rotate(45deg);
                margin-left: 7px;
                margin-top: 22px;
            }
            .right::after {
                width: 20px;
                height: 20px;
                border: 2px solid #999;
                border-left: none;
                border-bottom: none;
                content: '';
                display: block;
                transform: rotate(45deg);
                margin-right: 7px;
                margin-top: 19px;
            }
        }
    }
}

p img {
    width: 60px;
    height: 60px;
}

.deploy {
    float: left;
    width: 726px;
    height: 262px;
    margin-left: 20px;
    .deploy_title {
        width: 100%;
        height: 26px;
        margin: 10px 0;
        @include fontS(26px, 16px, #333);
        text-align: left;
    }
    .deploy_price {
        width: 100%;
        height: 80px;
        background: #f8f8f8;
        .span1 {
            width: 48px;
            height: 80px;
            display: inline-block;
            margin-left: 10px;
            float: left;
            @include fontS(80px, 12px, #999);
            text-align: left;
            span {
                margin-left: 20px;
            }
        }
        .span2 {
            float: left;
            height: 80px;
            font-weight: 700;
            display: inline-block;
            margin-left: 10px;
            @include fontS(62px, 16px, #fc002e);
            text-align: left;
            span {
                // width: ;
                font-size: 30px;
            }
        }
    }
    .shul {
        width: 100%;
        height: 32px;
        margin-top: 10px;
        .span1 {
            width: 48px;
            height: 32px;
            display: block;
            margin-left: 10px;
            float: left;
            @include fontS(30px, 12px, #999);
            text-align: left;
            span {
                margin-left: 20px;
            }
        }
        .jushu {
            float: left;
            width: 649px;
            height: 32px;
            margin-left: 20px;
            p {
                float: left;
                width: 20px;
                height: 30px;
                border: 1px solid #ddd;
                position: relative;
                float: left;
                cursor: pointer;
                @include fontS(30px, 12px, black);
            }
            p:nth-of-type(2) {
                width: 30px;
                margin: 0 5px;
            }
        }
    }
    .wares_btn {
        width: 100%;
        height: 44px;
        margin-top: 39px;
        button {
            width: 150px;
            height: 44px;
            line-height: 44px;
            cursor: pointer;
            text-align: center;
            font-size: 16px;
            background: linear-gradient(90deg, #fc4164, #fc002d);
            border: none;
            color: #fff;
            margin-left: 79px;
        }
    }
}

.jushu {
    a {
        color: #999;
    }
}