@charset "utf-8";

/*footer*/

#footer_box {
    width: 100%;
    background: #302e2e;
    .footer {
        @include gongy(120px);
        padding-top: 20px;
        .qrcode {
            float: right;
            img {
                width: 100px;
                height: 100px
            }
        }
        .concat {
            width: 1100px;
            height: 24px;
            margin-top: 10px;
            font-weight: 700;
            @include fontS(24px, 14px, #999);
            text-align: left;
            span {
                display: inline-block;
                margin-left: 10px;
                vertical-align: top;
            }
        }
        .copy {
            @include fontS(12px, 12px, #999);
            text-align: left;
            margin-top: 18px;
            span {
                margin-right: 15px;
            }
        }
    }
}